@use "../theme" as stk;

@mixin _build-global-variables(
  $suffix,
  $color-black,
  $color-white,
  $color-brand-blue,
  $color-brand-cornflower,
  $color-brand-charcoal,
  $color-brand-slate,
  $color-brand-steel,
  $color-brand-stone,
  $color-rank-super-green,
  $color-rank-sucker-red,
  $color-rank-strong-green,
  $color-rank-amber,
  $color-rank-warm-yellow,
  $color-rank-neutral,
  $color-accent-value-teal,
  $color-accent-bargain-purple,
  $color-accent-income-gold,
  $color-text-1,
  $color-text-2,
  $color-text-3,
  $color-text-invert,
  $color-action,
  $color-action-highlight,
  $color-action-hover,
  $color-danger,
  $color-danger-highlight,
  $color-danger-hover,
  $color-success,
  $color-success-highlight,
  $color-success-hover,
  $border-color,
  $shadow-subtle,
  $shadow-floating
) {
  // Black and white
  --stk-color-black#{$suffix}: #{$color-black};
  --stk-color-white#{$suffix}: #{$color-white};

  // Brand colors
  --stk-color-brand-blue#{$suffix}: #{$color-brand-blue};
  --stk-color-brand-cornflower#{$suffix}: #{$color-brand-cornflower};
  --stk-color-brand-charcoal#{$suffix}: #{$color-brand-charcoal};
  --stk-color-brand-slate#{$suffix}: #{$color-brand-slate};
  --stk-color-brand-steel#{$suffix}: #{$color-brand-steel};
  --stk-color-brand-stone#{$suffix}: #{$color-brand-stone};

  // Primary rank colors
  --stk-color-rank-super-green#{$suffix}: #{$color-rank-super-green};
  --stk-color-rank-sucker-red#{$suffix}: #{$color-rank-sucker-red};
  --stk-color-rank-strong-green#{$suffix}: #{$color-rank-strong-green};
  --stk-color-rank-amber#{$suffix}: #{$color-rank-amber};
  --stk-color-rank-warm-yellow#{$suffix}: #{$color-rank-warm-yellow};
  --stk-color-rank-neutral#{$suffix}: #{$color-rank-neutral};

  // Accent colors
  --stk-color-accent-value-teal#{$suffix}: #{$color-accent-value-teal};
  --stk-color-accent-bargain-purple#{$suffix}: #{$color-accent-bargain-purple};
  --stk-color-accent-income-gold#{$suffix}: #{$color-accent-income-gold};

  // Typographic colors
  --stk-color-text-1#{$suffix}: #{$color-text-1};
  --stk-color-text-2#{$suffix}: #{$color-text-2};
  --stk-color-text-3#{$suffix}: #{$color-text-3};
  --stk-color-text-invert#{$suffix}: #{$color-text-invert};

  // UI colors
  --stk-color-action#{$suffix}: #{$color-action};
  --stk-color-action#{$suffix}-highlight: #{$color-action-highlight};
  --stk-color-action#{$suffix}-hover: #{$color-action-hover};
  --stk-color-danger#{$suffix}: #{$color-danger};
  --stk-color-danger#{$suffix}-highlight: #{$color-danger-highlight};
  --stk-color-danger#{$suffix}-hover: #{$color-danger-hover};
  --stk-color-success#{$suffix}: #{$color-success};
  --stk-color-success#{$suffix}-highlight: #{$color-success-highlight};
  --stk-color-success#{$suffix}-hover: #{$color-success-hover};

  // Border
  --stk-border-color#{$suffix}: #{$border-color};

  // Shadow
  --stk-shadow-subtle#{$suffix}: #{$shadow-subtle};
  --stk-shadow-floating#{$suffix}: #{$shadow-floating};
}

@mixin buildLightTheme($suffix: '') {
  @include _build-global-variables(
    $suffix,
    stk.$color-black,
    stk.$color-white,
    stk.$color-brand-blue,
    stk.$color-brand-cornflower,
    stk.$color-brand-charcoal,
    stk.$color-brand-slate,
    stk.$color-brand-steel,
    stk.$color-brand-stone,
    stk.$color-rank-super-green,
    stk.$color-rank-sucker-red,
    stk.$color-rank-strong-green,
    stk.$color-rank-amber,
    stk.$color-rank-warm-yellow,
    stk.$color-rank-neutral,
    stk.$color-accent-value-teal,
    stk.$color-accent-bargain-purple,
    stk.$color-accent-income-gold,
    stk.$color-text-1,
    stk.$color-text-2,
    stk.$color-text-3,
    stk.$color-text-invert,
    stk.$color-action,
    stk.$color-action-highlight,
    stk.$color-action-hover,
    stk.$color-danger,
    stk.$color-danger-highlight,
    stk.$color-danger-hover,
    stk.$color-success,
    stk.$color-success-highlight,
    stk.$color-success-hover,
    stk.$border-color,
    stk.$shadow-subtle,
    stk.$shadow-floating
  );
}

@mixin buildDarkTheme($suffix: '') {
  @include _build-global-variables(
    $suffix,
    stk.$color-black-invert,
    stk.$color-white-invert,
    stk.$color-brand-blue-invert,
    stk.$color-brand-cornflower-invert,
    stk.$color-brand-charcoal-invert,
    stk.$color-brand-slate-invert,
    stk.$color-brand-steel-invert,
    stk.$color-brand-stone-invert,
    stk.$color-rank-super-green-invert,
    stk.$color-rank-sucker-red-invert,
    stk.$color-rank-strong-green-invert,
    stk.$color-rank-amber-invert,
    stk.$color-rank-warm-yellow-invert,
    stk.$color-rank-neutral-invert,
    stk.$color-accent-value-teal-invert,
    stk.$color-accent-bargain-purple-invert,
    stk.$color-accent-income-gold-invert,
    stk.$color-text-1-invert,
    stk.$color-text-2-invert,
    stk.$color-text-3-invert,
    stk.$color-text-invert-invert,
    stk.$color-action-invert,
    stk.$color-action-invert-highlight,
    stk.$color-action-invert-hover,
    stk.$color-danger-invert,
    stk.$color-danger-invert-highlight,
    stk.$color-danger-invert-hover,
    stk.$color-success-invert,
    stk.$color-success-invert-highlight,
    stk.$color-success-invert-hover,
    stk.$border-color-invert,
    stk.$shadow-subtle-invert,
    stk.$shadow-floating-invert
  );
}
