@use "../theme" as stk;

html,
body {
  background: var(--stk-color-white);
  font-family: Lato, Helvetica Neue, Arial, Helvetica, sans-serif;
  font-size: var(--stk-text-base);
  font-weight: var(--stk-text-weight-normal);
  line-height: var(--stk-text-line-base);
  color: var(--stk-color-text-1);
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
figure,
blockquote,
img,
hr {
  hyphens: auto;
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
figure,
blockquote,
img,
table,
hr {
  & + p,
  & + ul,
  & + ol,
  & + figure,
  & + blockquote,
  & + img,
  & + table,
  & + hr {
    margin-top: var(--stk-size-4);
  }
}

p,
figure,
blockquote,
img,
table,
hr {
  & + h1,
  & + h2,
  & + h3,
  & + h4,
  & + h5,
  & + h6 {
    margin-top: var(--stk-size-6);
  }
}

ul,
ol {
  & + p,
  & + figure,
  & + blockquote,
  & + img,
  & + table,
  & + hr {
    margin-top: var(--stk-size-5);
  }
}

p,
figure,
blockquote,
img,
table,
hr {
  & + ol,
  & + ul {
    margin-top: var(--stk-size-5);
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: var(--stk-text-line-compact);
  font-weight: var(--stk-text-weight-bold);
}

h1 {
  font-size: var(--stk-text-h1);
}

h2 {
  font-size: var(--stk-text-h2);
}

h3 {
  font-size: var(--stk-text-h3);
}

h4 {
  font-size: var(--stk-text-h4);
}

h5 {
  font-size: var(--stk-text-h5);
}

h6 {
  font-size: var(--stk-text-h6);
}

li + li {
  margin-top: var(--stk-size-3);
}

img {
  max-width: 100%;
}

blockquote {
  --blockquote-open-image: url('data:image/svg+xml;utf8,<svg width="24" height="24" xmlns="http://www.w3.org/2000/svg"><path d="M5.365 21.847c2.682 0 4.8-1.976 4.8-4.588 0-1.977-1.27-3.6-3.177-4.094C4.8 12.67 4.73 12.6 4.73 11.47c0-2.471 1.836-4.8 5.365-6.777L8.682 3C3.247 5.541 0 10.27 0 15.494c0 3.67 2.259 6.353 5.365 6.353zM19.2 21.847c2.682 0 4.8-1.976 4.8-4.588 0-1.977-1.27-3.6-3.176-4.094-2.189-.494-2.26-.565-2.26-1.694 0-2.471 1.836-4.8 5.365-6.777L22.518 3c-5.436 2.541-8.683 7.27-8.683 12.494 0 3.67 2.26 6.353 5.365 6.353z"/></svg>');
  --blockquote-close-image: url('data:image/svg+xml;utf8,<svg width="24" height="24" xmlns="http://www.w3.org/2000/svg"><path d="M1.482 21.847c5.365-2.541 8.683-7.27 8.683-12.423C10.165 5.612 7.976 3 4.8 3 2.118 3 0 4.976 0 7.588c0 1.977 1.27 3.6 3.176 4.094 2.189.494 2.26.565 2.26 1.694 0 2.471-1.836 4.73-5.365 6.777l1.411 1.694zM15.318 21.847C20.682 19.306 24 14.577 24 9.424 24 5.612 21.812 3 18.635 3c-2.682 0-4.8 1.976-4.8 4.588 0 1.977 1.27 3.6 3.177 4.094 2.188.494 2.259.565 2.259 1.694 0 2.471-1.836 4.73-5.365 6.777l1.412 1.694z"/></svg>');
  --blockquote-icon-size: var(--stk-size-3);

  padding: var(--stk-size-5);
  font-style: italic;
  position: relative;
  background: var(--stk-color-brand-stone);
  border-radius: var(--stk-border-radius-small);

  &::before,
  &::after {
    display: block;
    position: absolute;
    opacity: 0.5;
    content: ' ';
    width: var(--blockquote-icon-size);
    height: var(--blockquote-icon-size);
    -webkit-mask-size: var(--blockquote-icon-size);
    mask-size: var(--blockquote-icon-size);
    -webkit-mask-image: var(--blockquote-quote-image);
    mask-image: var(--blockquote-quote-image);
    background-color: var(--stk-color-brand-slate);
  }

  &::before {
    --blockquote-quote-image: var(--blockquote-open-image);

    top: var(--stk-size-2);
    left: var(--stk-size-2);
  }

  &::after {
    --blockquote-quote-image: var(--blockquote-close-image);

    bottom: var(--stk-size-2);
    right: var(--stk-size-2);
  }
}

figcaption {
  --figcaption-distance: var(--stk-size-2);

  &:first-child {
    margin-bottom: var(--figcaption-distance);
  }

  &:not(:first-child) {
    margin-top: var(--figcaption-distance);
  }
}

a {
  color: var(--stk-color-action);
  text-decoration: none;

  &:hover,
  &:visited {
    color: var(--stk-color-action-hover);
  }
}

hr {
  border: 0;
  height: var(--stk-border-width);
  background: var(--stk-border-color);
}
