@use "../theme" as stk;
@use "../utils" as utils;
@use "../utils/_global-variables" as globals;

$include-dark-mode: false !default;

*,
*::before,
*::after {
  box-sizing: border-box;
}

:root {
  @include globals.buildLightTheme();
  @include globals.buildLightTheme('-static');

  // Size
  --stk-size-1: #{stk.$size-1};
  --stk-size-2: #{stk.$size-2};
  --stk-size-3: #{stk.$size-3};
  --stk-size-4: #{stk.$size-4};
  --stk-size-5: #{stk.$size-5};
  --stk-size-6: #{stk.$size-6};
  --stk-size-7: #{stk.$size-7};
  --stk-size-8: #{stk.$size-8};

  // Font
  --stk-text-family: #{stk.$text-family};
  --stk-text-weight-bold: #{stk.$text-weight-bold};
  --stk-text-weight-normal: #{stk.$text-weight-normal};
  --stk-text-line-compact: #{stk.$text-line-compact};
  --stk-text-line-base: #{stk.$text-line-base};
  --stk-text-title: #{stk.$text-title};
  --stk-text-h1: #{stk.$text-h1};
  --stk-text-h2: #{stk.$text-h2};
  --stk-text-h3: #{stk.$text-h3};
  --stk-text-h4: #{stk.$text-h4};
  --stk-text-h5: #{stk.$text-h5};
  --stk-text-h6: #{stk.$text-h6};
  --stk-text-large: #{stk.$text-large};
  --stk-text-base: #{stk.$text-base};
  --stk-text-small: #{stk.$text-small};
  --stk-text-tiny: #{stk.$text-tiny};
  --stk-text-micro: #{stk.$text-micro};

  // Border
  --stk-border-radius-regular: #{stk.$border-radius-regular};
  --stk-border-radius-small: #{stk.$border-radius-small};
  --stk-border-width: #{stk.$border-width};
}

@if ($include-dark-mode) {
  @media (prefers-color-scheme: dark) {
    :root {
      @include globals.buildDarkTheme();
    }
  }
}
