@use '../utils' as utils;

$vertical-spacing: var(--stk-size-1) !default;
$border-width: var(--stk-border-width) !default;
$chevron-size: 0.5em !default;
$chevron-thickness: 0.1em !default;
$button-padding: var(--stk-size-1) !default;
$button-size: var(--stk-size-6) !default;
$button-text-size: var(--stk-text-small) !default;
$accent: var(--stk-color-action) !default;
$accent-highlight: var(--stk-color-action-highlight) !default;
$accent-disabled: rgba(0, 0, 0, 0.15) !default;
$accent-disabled-font: var(--stk-color-text-3) !default;

.stk-pagination {
  --stk-pagination-theme: #{$accent};
  --stk-pagination-border-radius: var(--stk-border-radius-small);
  --stk-pagination-button-padding: #{$button-padding};
  --stk-pagination-button-size: #{$button-size};
  --stk-pagination-button-background: transparent;
  --stk-pagination-button-hover-background: #{$accent-highlight};
  --stk-pagination-button-text-size: #{$button-text-size};
  --stk-pagination-button-text-color: var(--stk-color-text-2);
  --stk-pagination-button-selected-background: var(--stk-pagination-theme);
  --stk-pagination-button-selected-text-color: var(--stk-color-text-invert);
  --stk-pagination-chevron-size: #{$chevron-size};
  --stk-pagination-chevron-thickness: #{$chevron-thickness};
  --stk-pagination-chevron-color: var(--stk-pagination-button-text-color);

  display: inline-flex;
  flex-wrap: wrap;
  margin-top: calc(#{$vertical-spacing} * -1);
}

.stk-pagination__button {
  @include utils.button-reset;

  &,
  &:link,
  &:visited {
    color: var(--stk-pagination-button-text-color);
    background: var(--stk-pagination-button-background);
    border: var(--stk-border-width) solid var(--stk-border-color);
    cursor: pointer;
  }

  &:hover {
    --stk-pagination-button-background: var(--stk-pagination-button-hover-background);
  }
}

.stk-pagination__button--first {
  border-top-left-radius: var(--stk-pagination-border-radius);
  border-bottom-left-radius: var(--stk-pagination-border-radius);
}

.stk-pagination__button--last {
  border-top-right-radius: var(--stk-pagination-border-radius);
  border-bottom-right-radius: var(--stk-pagination-border-radius);
}

.stk-pagination__separator,
.stk-pagination__button {
  padding: 0 var(--stk-pagination-button-padding);
  margin-top: $vertical-spacing;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: calc(#{$border-width} * -1);
  min-width: var(--stk-pagination-button-size);
  height: var(--stk-pagination-button-size);
  font-size: var(--stk-pagination-button-text-size);
}

.stk-pagination__button--prev,
.stk-pagination__button--next {
  position: relative;
  text-align: left;
  text-indent: -9999px;

  &::after {
    --stk-pagination-chevron-position: calc(50% - (var(--stk-pagination-chevron-size) / 2) - var(--stk-pagination-chevron-thickness));
    --stk-pagination-chevron-arm: var(--stk-pagination-chevron-thickness) solid var(--stk-pagination-chevron-color);

    content: " ";
    display: block;
    position: absolute;
    top: calc(50% - (var(--stk-pagination-chevron-size) / 2));
    width: var(--stk-pagination-chevron-size);
    height: var(--stk-pagination-chevron-size);
    transform: rotate(45deg);
  }
}

.stk-pagination__button--prev::after {
  right: var(--stk-pagination-chevron-position);
  border-bottom: var(--stk-pagination-chevron-arm);
  border-left: var(--stk-pagination-chevron-arm);
}

.stk-pagination__button--next::after {
  left: var(--stk-pagination-chevron-position);
  border-top: var(--stk-pagination-chevron-arm);
  border-right: var(--stk-pagination-chevron-arm);
}

.stk-pagination__button--selected {
  &,
  &:hover,
  &:focus {
    --stk-pagination-button-background: var(--stk-pagination-button-selected-background);
    --stk-pagination-button-text-color: var(--stk-pagination-button-selected-text-color);

    z-index: 2;
  }
}

.stk-pagination__button:disabled,
.stk-pagination__button--disabled, {
  &,
  &:hover,
  &:focus {
    --stk-pagination-theme: #{$accent-disabled};
    --stk-pagination-button-text-color: #{$accent-disabled-font};
    --stk-pagination-chevron-color: #{$accent-disabled-font};
    --stk-pagination-button-background: transparent;

    cursor: default;
  }
}
