
table {
  --table-border-radius: var(--stk-border-radius-small);
  --table-header-padding: var(--stk-size-2);
  --table-data-padding: var(--stk-size-1) var(--stk-size-2);

  background: transparent;
  border: var(--stk-border-width) solid var(--stk-border-color);
  border-collapse: separate;
  border-spacing: 0;
  border-radius: var(--table-border-radius);
  font-size: var(--stk-text-small);
}

tr:first-child {
  th {
    border-bottom: var(--stk-border-width) solid var(--stk-border-color);
  }

  th:first-child {
    border-top-left-radius: var(--table-border-radius);
  }

  th:last-child {
    border-top-right-radius: var(--table-border-radius);
  }
}

td:first-child {
  border-bottom-left-radius: var(--table-border-radius);
}

td:last-child {
  border-bottom-left-radius: var(--table-border-radius);
}

th,
td {
  text-align: left;
}

th {
  padding: var(--table-header-padding);
  background: var(--stk-color-brand-stone);
  font-weight: var(--stk-text-weight-normal);
  line-height: var(--stk-text-line-compact);
}

td {
  padding: var(--table-data-padding);
  line-height: var(--stk-text-line-base);
}
